import { Component, OnInit, ViewChild } from '@angular/core';
import { Router,NavigationExtras } from '@angular/router';
import { NgForm, NgModel, NgControl, FormsModule } from '@angular/forms';
import { LoopBackAuth, App_loginApi, BooksApi } from '../../../../shared/loopback-sdk';
import { ActivatedRoute } from "@angular/router";
import * as moment from 'moment'; //for date manupalation
import {ConfirmationService} from 'primeng/primeng';
import { Lightbox } from 'angular2-lightbox';
import {PaginatorModule} from 'primeng/primeng';
import {Message,MenuItem,SelectItem} from 'primeng/primeng';
import { CookieService } from 'angular2-cookie/core';
declare var window:any;
declare var $;
declare var sdkPrefix;
@Component({
	selector: "View Page",
	templateUrl: 'view-Page.component.html',
	providers: [ConfirmationService]
})

export class ViewPageComponent {
	result: any;
	success: boolean;
	display: boolean;
	public id: number;
	public role: string;
	submited:any=false;
	@ViewChild('fullName') fullName: any; 
	@ViewChild('userNameMobile') userNameMobile: any; 
	@ViewChild('eMail') eMail: any; 

	pageData: any = [];
	backLink:any = "";
	busy:any;
	emptyMessage:String="No Result Found";
	displayConfirm: boolean = false;
	msgs: Message[] = [];
	pageStatus:any="";
	pageName:any;
	maxDate:Date;
	loading:boolean = true;
	chapterName:any;
	chapterStatus:any="";
	createdAt:any;
	chapterNameSearch:any;
	chapterStatusSearch:any="";
	createdAtSearch:any;
	paginationVal:any=false;
	perPageLimit:Number = 10;
	offset:Number=0;
	total:number;
	_albums:any = [];
	chapterData:any=[];
	ifEmpty:boolean=false;
	constructor(private router:Router, private pageApi:BooksApi, private _cookieService: CookieService, private _lightbox: Lightbox, private confirmationService: ConfirmationService, private appLoginApi:App_loginApi,private loopAuth:LoopBackAuth,private route: ActivatedRoute) {
		this.id = this.route.snapshot.params['id'];
	}

	ngOnInit(): void {
		let dataObj={};
		let uid=this.id;
		let dataArrObj={
			"id":this.id
		}
		let loginFlagToken = this._cookieService.get(sdkPrefix+'id');
		this.busy=this.pageApi.getPages(dataArrObj).subscribe(
			data=>{
				if(data.result.length>0){
					let result = data.result[0];
					let dataDesc = result.content;
					this.replaceImageUrl(dataDesc).then((resultData)=>{
						if(result.status == 1){
							result.status = 'Active';
						}else{
							result.status = 'Inactive';
						}

						if(result.content){
							result.content = resultData;
						}

						if(result.created_at){
							result.created_at = moment(result.created_at).format("DD - MMM - YYYY");
						}
						this.pageData = result;
					});
				}
			},
			err=>{

			},
			()=>{

			}
		);
	}

	activeInactiveClick(){
		this.confirmationService.confirm({
			message: 'Are you sure that you want to proceed?',
			header: 'Confirmation',
			icon: 'fa fa-question-circle',
			accept: () => {
			  // this.msgs = [{severity:'info', summary:'Confirmed', detail:'You have accepted'}];
			  this.activeInactiveSubmit();
			},
			reject: () => {
			 // this.msgs = [{severity:'info', summary:'Rejected', detail:'You have rejected'}];
			}
		});
	}

	activeInactiveSubmit(){
		let dataObj={};
		if(this.pageData['status']=='Active'){
			dataObj['status']=0;
		}else{
			dataObj['status']=1;
		}
		let pageId=this.id;
		this.msgs.length=0;
		this.busy = this.pageApi.addEditPages(dataObj,pageId).subscribe((respData:any)=>{
			this.msgs.push({severity:'success', summary:'Success Message', detail:'Book Status Has Been Changed Successfully.'});
			setTimeout(()=>{
				window.location.reload();
			},3000);
		},(error)=>{
			//console.log('respData error',error);
			this.msgs.push({severity:'warn', summary:'Error Message', detail:'Somthing Went Wrong. Please try again later.'});
		});
	}

	replaceImageUrl(content){
		return new Promise((resolve,reject)=>{
			const parser = new DOMParser();
			let loginFlagToken = this._cookieService.get(sdkPrefix+'id');
			let content1 = parser.parseFromString(content,"text/html");
			if(content1.images.length > 0){
				for(let i=0;i<content1.images.length;i++){
					let tempImageSrc = "";
					let imageSrc = "";
					tempImageSrc = content1.images[i].src;
					imageSrc = content1.images[i].src+'?access_token='+loginFlagToken;
					content = content.replace(tempImageSrc,imageSrc);
				}
				resolve(content);
			}else{
				resolve(content);
			}
		});
	}
}


